@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
/* Minimo CSS Reset */
html {
  box-sizing: border-box;
  font-size: 16px;
}

html,
body {
  height: 100%;
  min-height: 100vh;
  overflow: hidden;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
a {
  color: white;
}
ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

.App {
  font-family: sans-serif;
  background-color: #0079bf;
  height: 100%;
  min-height: 100vh;
  color: white;
  overflow: hidden;
}
/* CSS adicional */

.content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100vw;
  position: absolute;
  bottom: 0;
  top: 116px;
  right: 0;
  left: 0;
}
.lefticons {
  display: none;
  color: white;
}
.lefticons svg {
  padding: 10px;
}
.righticons {
  display: none;
  color: #003b5d;
}
.righticons svg {
  padding: 10px;
}
@media only screen and (max-width: 600px) {
  .leftside {
    position: absolute;
    left: 0;
    z-index: 400;
    background-color: #0079bf;
  }
  .rightside {
    position: absolute;
    right: 0;
    //left: 50px;
    z-index: 500;
    width: calc(100vw - 50px);
  }
  .leftwrap {
    transition: all 0.3s ease;
    -webkit-transform: scalex(0);
            transform: scalex(0);
    -webkit-transform-origin: 85% right;
            transform-origin: 85% right;
    opacity: 0;
  }
  .lefticons {
    position: fixed;
    display: flex;
    flex-direction: column;
    bottom: 10px;
    left: 5px;
  }
  .rightwrap {
    transition: all 0.3s ease;
    -webkit-transform: scalex(1);
            transform: scalex(1);
    -webkit-transform-origin: 85% left;
            transform-origin: 85% left;
    opacity: 1;
  }
  /*leftside*/

  .leftside:hover {
    position: absolute;
    z-index: 600;
    box-shadow: 0 20px 36px 0 rgb(0 0 0 / 20%), 0 36px 20px 0 rgb(0 0 0 / 19%) !important;
  }
  .leftside:hover .lefticons {
    display: none;
  }
  .leftside:hover .leftwrap {
    transition: all 0.3s ease;
    -webkit-transform: scalex(1);
            transform: scalex(1);
    -webkit-transform-origin: 100% left;
            transform-origin: 100% left;
    opacity: 1;
  }
}

.headerbar {
  background-color: rgba(0, 0, 0, 0.3);
  padding: 0;
  color: white;
  font-weight: bold;
  font-size: 18px;
}
.titlebar {
  display: flex;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 10px;
}
.headerwrap {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
}
img {
  border-radius: 50%;
  width: 50px;
  height: 50px;
}
.name {
  font-size: 24px;
  padding: 10px;
}
.info {
  text-align: right;
  font-family: "Montserrat", sans-serif;
  opacity: 0;
  position: absolute;
  right: 0;
  width: 100%;
  padding: 10px;
  background-color: grey;
  transition: all 0.3s ease;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  -webkit-transform-origin: 85% top;
          transform-origin: 85% top;
  z-index: 1000;
}
.headerbar:hover .info {
  opacity: 1;
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}
.info > div {
  display: flex;
  flex-direction: row-reverse;
  padding-right: 20px;
}
